/**
 * Powerup Component
 *
 * Implement controlling powerups of the game.
 * Requires randomSpawn Component.
 *
 * @property {string} type - Powerup type, same as configured in the backend.
 * @property {string} assetId - Id of the a-asset-item (not a selector).
 * @property {number} displayDuration - Duration in seconds the powerup is shown to the player.
 * @property {number} spawnProbability - Probability of spawning.
 *
 */

import AFRAME from 'aframe';
import store from '@/store';
import { powerUpTypes } from '@/helpers/enums';

AFRAME.registerComponent('powerup', {
  schema: {
    type: { type: 'string' },
    assetId: { type: 'string' },
    displayDuration: { type: 'number', default: 10 },
    spawnProbability: { type: 'number', default: 25 },
  },
  init() {
    const { el, data } = this;

    el.setAttribute('gltf-model', `#${data.assetId}`);

    this.collect = this.collect.bind(this);
    el.addEventListener('die', this.collect);

    this.spawn();
  },
  spawn() {
    const { el, data } = this;
    clearInterval(this.spawnInterval);

    this.spawnInterval = setInterval(() => {
      const score = store.getters['player/score'];
      const isPlaying = store.getters['game/isPlaying'];
      const amount = Math.random() * 100;

      if (!isPlaying) return;

      if (score >= 300 && data.type === powerUpTypes.HEALTH) {
        data.spawnProbability *= 2;
      }

      if (amount <= data.spawnProbability) {
        el.emit('spawn');
        this.display();
        clearInterval(this.spawnInterval);
      }
    }, 5000);
  },
  display() {
    const { el, data } = this;

    this.displayTimeout = setTimeout(() => {
      this.reSpawn(el);
    }, data.displayDuration * 1000);
  },
  reSpawn(el) {
    el.setAttribute('visible', 'false');
    el.setAttribute('position', '100 100 100');
    this.spawn();
  },
  collect() {
    const { el, data } = this;

    if (data.type === powerUpTypes.HEALTH) {
      store.dispatch('player/incrementHealth', 20);
    } else {
      store.dispatch('player/addPowerup', data.type);
      this.activate();
    }

    this.reSpawn(el);
  },
  activate() {
    const { data } = this;

    this.activeTimeout = setTimeout(() => {
      store.dispatch('player/removePowerup', data.type);
    }, 5000);
  },
  remove() {
    clearInterval(this.spawnInterval);
    clearTimeout(this.displayTimeout);
    clearTimeout(this.activeTimeout);
  },
});
