/**
 * Enemy Component
 *
 * Implement controlling the enemies of the game.
 * Requires randomSpawn Component.
 *
 * @property {string} assetId - Id of the a-asset-item (not a selector).
 * @property {number} playerDamage - Amount of damage it inflicts on player.
 * @property {number} score - Score amount it gives to the player if killed.
 * @property {number} spawnProbability - Probability of spawning.
 * @property {number} spawnMinScore - Minimum score value for spawning enemy.
 * @property {number} spawnMaxScore - Maximum score value for spawning enemy.
 *
 */

import AFRAME from 'aframe';
import store from '@/store';
import { powerUpTypes } from '@/helpers/enums';

AFRAME.registerComponent('enemy', {
  schema: {
    assetId: { type: 'string' },
    playerDamage: { type: 'number', default: 15 },
    score: { type: 'number', default: 10 },
    spawnProbability: { type: 'number', default: 50 },
    spawnMinScore: { type: 'number', default: 0 },
    spawnMaxScore: { type: 'number', default: 10 },
  },
  init() {
    const { el, data } = this;

    el.setAttribute('gltf-model', `#${data.assetId}`);

    this.damagePlayer = this.damagePlayer.bind(this);
    el.addEventListener('stateadded', this.damagePlayer);

    this.die = this.die.bind(this);
    el.addEventListener('die', this.die);
  },
  play() {
    this.spawn();
  },
  die() {
    const { el, data } = this;
    const hasMultiplier = store.getters['player/activePowerups'].includes(powerUpTypes.SCORE_MULTIPLIER);
    const score = hasMultiplier ? data.score * 2 : data.score;

    this.reSpawn(el);
    store.dispatch('player/incrementScore', score);
  },
  damagePlayer() {
    const { el, data } = this;

    if (el.is('collided')) {
      this.reSpawn(el);
      store.dispatch('player/decrementHealth', data.playerDamage);
    }
  },
  spawn() {
    const { el, data } = this;
    clearInterval(this.spawnInterval);

    this.spawnInterval = setInterval(() => {
      const score = store.getters['player/score'];
      const isPlaying = store.getters['game/isPlaying'];
      const amount = Math.random() * 100;

      if (!isPlaying || score < data.spawnMinScore || score > data.spawnMaxScore) {
        return;
      }

      if (amount <= data.spawnProbability) {
        el.emit('spawn');
        clearInterval(this.spawnInterval);
      }
    }, 5000);
  },
  reSpawn(el) {
    el.setAttribute('visible', 'false');
    el.setAttribute('position', '100 100 100');
    el.emit('despawn');
    this.spawn();
  },
  remove() {
    clearInterval(this.spawnInterval);
    this.el.removeEventListener('stateadded', this.damagePlayer);
  },
});
