<template>
  <div class="game">
    <a-scene
      stats
      vr-mode-ui="enabled: false"
    >
      <a-assets ref="assets">
        <a-asset-item id="virus1" src="static_assets/models/Virus01.glb"></a-asset-item>
        <a-asset-item id="avocado" src="static_assets/models/Avocado.glb"></a-asset-item>
      </a-assets>

      <!-- Player Bullets -->
      <a-entity
        bullet="name: normal; speed: 30.0; poolSize: 20"
        geometry="primitive: icosahedron; radius: 0.1;"
        material="color: #000; emissiveIntensity: 0.5; emissive: #000">
      </a-entity>

      <a-entity
        bullet="name: doubleBullets; speed: 30.0; poolSize: 20; damagePoints: 2.0"
        geometry="primitive: icosahedron; radius: 0.1;"
        material="color: #000; emissiveIntensity: 0.5; emissive: #000">
      </a-entity>

      <!-- Enemy Bullet -->
      <a-entity
        bullet="name: enemy; speed: 10.0; poolSize: 20"
        geometry="primitive: icosahedron; radius: 0.1;"
        material="color: #ff0000; emissiveIntensity: 0.5; emissive: #ff0000">
      </a-entity>

      <!-- Enemies -->
      <a-entity
        v-for="item in 10"
        :key="item"
        :id="`enemy${item}`"
        class="enemies"
        animation-mixer
        target="static: false"
        enemy="assetId: virus1;"
        random-spawn="min: -10 -1 -10;
                      max: 10 2 10;
                      radius: 2"
        visible="false"
        animation="property: position;
                  to: 0 1.6 0;
                  dur: 20000;
                  easing: easeInOutCubic;
                  startEvents: spawn;
                  pauseEvents: despawn"
        proxy-event__spawn="event: spawn; to: CHILDREN"
        proxy-event__despawn="event: despawn; to: CHILDREN"
        camera="active: false"
        look-at="#player"
      >
        <a-entity
          shooter="activeBulletType: enemy"
          enemy-shooter
          position="0 0 -1"
        >
        </a-entity>
      </a-entity>

      <!-- Powerups -->
      <a-entity
        v-for="item in powerups"
        :key="item"
        :id="item"
        target="static: false"
        powerup="type: DoubleBullets; assetId: avocado"
        random-spawn="min: -10 -1 -10;
                      max: 10 2 10;
                      radius: 2"
        visible="false"
        scale="5 5 5"
      ></a-entity>

      <!-- Player -->
      <a-entity
        id="player"
        sphere-collider="objects: .enemies; radius: 0.5"
        target
        geometry="primitive: sphere; radius: 0.5"
        position="0 1.6 0"
        proxy-event="event: die; to: #gun;"
      >
        <a-camera
          active="true"
          position="0 0 0"
        >
          <a-entity
            id="gun"
            position="0 0 -1"
            shooter="activeBulletType: normal"
            player="ammo: 10"
          >
          </a-entity>
        </a-camera>
      </a-entity>
    </a-scene>

    <component :is="hudComponent"></component>
  </div>
</template>

<script>
import 'aframe';
import 'aframe-super-shooter-kit';
import 'aframe-extras';
import 'aframe-proxy-event-component';
import 'aframe-look-at-component';

// Aframe Components
import './components/player';
import './components/enemy';
import './components/enemyShooter';
import './components/randomSpawn';
import './components/powerup';

// Vue Components
import Hud from '@/components/Hud/Hud.vue';

// Utils
import camera from './utils/camera';

export default {
  name: 'Game',
  components: {
    Hud,
  },
  data() {
    return {
      hudComponent: undefined,
      powerups: [
        'powerup1',
        'powerup2',
        'powerup3',
      ],
    };
  },
  mounted() {
    const { assets } = this.$refs;

    assets.addEventListener('loaded', () => {
      this.hudComponent = Hud;
      camera.init();
    });
  },
};
</script>
